// Core
import React, { Component, createRef } from 'react';
import cx from 'classnames';

// Instruments
import Styles from './styles.m.css';

// Components
import Checkbox from 'theme/assets/Checkbox';
import Remove from 'theme/assets/Remove';
import Edit from 'theme/assets/Edit';
import Star from 'theme/assets/Star';

export default class Task extends Component {
    static getDerivedStateFromProps ({ message }, { mutated, newMessage }) {
        let nextState = null;

        if (!mutated) {
            nextState = {
                newMessage: message,
            };
        }

        if (message === newMessage) {
            nextState = {
                mutated: false,
            };
        }

        return nextState;
    }

    state = {
        newMessage: '',
        editing:    false,
        mutated:    false,
    };

    componentDidUpdate (_, { editing }) {
        !editing && this.input.current.focus();
    }

    input = createRef();

    _getTaskShape = ({
        id,
        completed = this.props.completed,
        favorite = this.props.favorite,
        message = this.props.message,
    }) => ({
        id,
        completed,
        favorite,
        message,
    });

    _editNewMessage = (event) => {
        this.setState({
            newMessage: event.target.value,
        });
    };

    _toggleEditingState = () => {
        this.setState(({ editing }) => ({
            editing: !editing,
        }));
    };

    _updateTodo = (event) => {
        const { newMessage } = this.state;
        const { _updateTask, id } = this.props;

        if (!newMessage.length) {
            return;
        }

        switch (event.key) {
            case 'Enter': {
                this.setState({
                    mutated: true,
                    editing: false,
                });
                _updateTask({
                    id,
                    message:   newMessage,
                    completed: false,
                    favorite:  false,
                });

                break;
            }

            case 'Escape': {
                this.setState({
                    message: newMessage,
                    editing: false,
                });

                break;
            }

            default:
                break;
        }
    };

    _completeTask = () => {
        const { id, _updateTask } = this.props;

        _updateTask(
            this._getTaskShape({ id, completed: !this.props.completed })
        );
    };

    _changePriority = () => {
        const { id, _updateTask } = this.props;

        _updateTask(this._getTaskShape({ id, favorite: !this.props.favorite }));
    };

    _removeTask = () => {
        const { id, _removeTask } = this.props;

        _removeTask(id);
    };

    render () {
        const { editing, newMessage, mutated } = this.state;
        const { message, completed, favorite } = this.props;

        const styles = cx(Styles.task, {
            [Styles.completed]: completed,
        });

        const currentMessage = editing || mutated ? newMessage : message;

        return (
            <li className = { styles }>
                <div className = { Styles.content }>
                    <Checkbox
                        inlineBlock
                        checked = { completed }
                        className = { Styles.complete }
                        color1 = '#3B8EF3'
                        color2 = '#FFF'
                        onClick = { this._completeTask }
                    />
                    <input
                        disabled = { !editing }
                        ref = { this.input }
                        type = 'text'
                        value = { currentMessage }
                        onChange = { this._editNewMessage }
                        onKeyDown = { this._updateTodo }
                    />
                </div>
                <div className = { Styles.actions }>
                    <Star
                        inlineBlock
                        checked = { favorite }
                        className = { Styles.setPriority }
                        color1 = '#3B8EF3'
                        color2 = '#000'
                        onClick = { this._changePriority }
                    />
                    <Edit
                        inlineBlock
                        checked = { editing }
                        className = { Styles.edit }
                        color1 = '#3B8EF3'
                        color2 = '#000'
                        onClick = { this._toggleEditingState }
                    />
                    <Remove
                        inlineBlock
                        className = { Styles.remove }
                        color1 = '#3B8EF3'
                        color2 = '#000'
                        onClick = { this._removeTask }
                    />
                </div>
            </li>
        );
    }
}
