// Core
import React, { Component } from 'react';

// Instruments
import { getDisplayName } from './helpers';
import { url, token } from 'config/api';
import initialState from './tasks'; // Это заглушка, ее нужно очистить при сдаче домашки

export const withState = (Enhanceable) => {
    class WithState extends Component {
        static getDerivedStateFromProps () {
            const tasks = JSON.parse(localStorage.getItem('tasks'));

            return tasks ? { tasks } : { ...initialState };
        }

        state = {
            newTaskMessage:  '',
            searchMessage:   '',
            isTasksFetching: false,
            taks:            [],
        };

        componentDidMount () {
            this._fetchTasks();
        }

        componentDidUpdate () {
            localStorage.setItem('tasks', JSON.stringify(this.state.tasks));
        }

        _fetchTasks = async () => {
            try {
                this._startTasksFetching();

                const response = await fetch(url, {
                    method:  'GET',
                    headers: {
                        Authorization: token,
                    },
                });

                const { data: tasks, message } = await response.json();

                if (response.status !== 200) {
                    throw new Error(message);
                }

                this.setState({
                    tasks: this._sortTasks(tasks),
                });

                this.setState();
            } catch (error) {
                console.log('tasks was not loaded', error);
            } finally {
                this._stopTasksFetching();
            }
        };

        _createTask = async (event) => {
            try {
                event.preventDefault();
                const { newTaskMessage } = this.state;

                if (!newTaskMessage) {
                    return null;
                }

                this._startTasksFetching();

                const response = await fetch(url, {
                    method:  'POST',
                    headers: {
                        Authorization:  token,
                        'Content-Type': 'application/json',
                    },
                    body: JSON.stringify({ message: newTaskMessage }),
                });

                const { data: task, message } = await response.json();

                if (response.status !== 200) {
                    throw new Error(message);
                }

                this.setState(({ tasks }) => ({
                    tasks:          [task, ...tasks],
                    newTaskMessage: '',
                }));
            } catch (error) {
                console.log('task was not created', error);
            } finally {
                this._stopTasksFetching();
            }
        };

        _updateTask = async (updatedTask) => {
            try {
                event.preventDefault();

                this._startTasksFetching();

                const response = await fetch(url, {
                    method:  'PUT',
                    headers: {
                        Authorization:  token,
                        'Content-Type': 'application/json',
                    },
                    body: JSON.stringify([updatedTask]),
                });

                const {
                    data: [taskFromResponse],
                    message: errorMessage,
                } = await response.json();

                if (response.status !== 200) {
                    throw new Error(errorMessage);
                }

                this.setState(({ tasks }) => {
                    const indexToReplace = tasks.indexOf(
                        tasks.find((task) => task.id === updatedTask.id)
                    );

                    const newTasks = [
                        ...tasks.filter((task) => task.id !== updatedTask.id)
                    ];

                    newTasks.splice(indexToReplace, 0, taskFromResponse);

                    const sortedTasks = this._sortTasks(newTasks);

                    return {
                        tasks: sortedTasks,
                    };
                });
            } catch (error) {
                console.log('task was not created', error);
            } finally {
                this._stopTasksFetching();
            }
        };

        _removeTask = async (id) => {
            try {
                event.preventDefault();

                this._startTasksFetching();

                const response = await fetch(`${url}/${id}`, {
                    method:  'DELETE',
                    headers: {
                        Authorization: token,
                    },
                });

                if (response.status !== 204) {
                    const { message: errorMessage } = await response.json();

                    throw new Error(errorMessage);
                }

                this.setState(({ tasks }) => ({
                    tasks: tasks.filter((task) => task.id !== id),
                }));
            } catch (error) {
                console.log('task was not deleted', error);
            } finally {
                this._stopTasksFetching();
            }
        };

        _startTasksFetching = () => {
            this.setState({
                isTasksFetching: true,
            });
        };

        _stopTasksFetching = () => {
            this.setState({
                isTasksFetching: false,
            });
        };

        _updateNewTaskMessage = (event) => {
            const newTaskMessage = event.target.value;

            newTaskMessage.length < 50 &&
                this.setState({
                    newTaskMessage,
                });
        };

        _updateSearchMessage = (event) => {
            this.setState({
                searchMessage: event.target.value.toLowerCase(),
            });
        };

        _sortTasks = (tasks) => {
            const favorite = tasks.filter(
                (task) => task.favorite && !task.completed
            );
            const usual = tasks.filter(
                (task) => !task.favorite && !task.completed
            );
            const completed = tasks.filter((task) => task.completed);

            const sortedCompleted = [
                ...completed.sort((a, b) => {
                    if (a.favorite && !b.favorite) {
                        return -1;
                    } else if (!a.favorite && b.favorite) {
                        return 1;
                    }

                    return false;
                })
            ];

            return [...favorite, ...usual, ...sortedCompleted];
        };

        _completeAll = () => {
            const { tasks } = this.state;

            const allCompleted = this._getAllCompleted();

            !allCompleted &&
                tasks.forEach((task) => {
                    this._updateTask({ ...task, completed: true });
                });
        };

        _getAllCompleted = () =>
            this.state.tasks.every((task) => Boolean(task.completed));

        render () {
            return (
                <Enhanceable
                    _completeAll = { this._completeAll }
                    _createTask = { this._createTask }
                    _getAllCompleted = { this._getAllCompleted }
                    _removeTask = { this._removeTask }
                    _updateNewTaskMessage = { this._updateNewTaskMessage }
                    _updateSearchMessage = { this._updateSearchMessage }
                    _updateTask = { this._updateTask }
                    { ...this.state }
                    { ...this.props }
                />
            );
        }
    }

    WithState.displayName = `withState(${getDisplayName(Enhanceable)})`;

    return WithState;
};
